:root {
    --background-color: black;
    --text-color: whitesmoke;
    --gold-color: #FFD700;
    --green-royal: #007A33;
}

body {
    margin: 0;
    background-color: var(--background-color);
    color: var(--text-color);
}

html, body {
    height: 100%;
    width: 100%;
}

.contact-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    direction: rtl; /* Apply RTL direction for text */
}

.contact-container h1 {
    text-align: center; /* Center "Contact Us" header */
    color: var(--gold-color);
    font-size: 2em;
    margin-bottom: 20px;
}

.contact-container p, 
.contact-details h3, 
.contact-details p {
    text-align: right; /* Align remaining text to the right */
    color: var(--text-color);
    line-height: 1.6;
}

.contact-details h3 {
    margin-top: 20px;
    color: var(--gold-color);
    font-weight: bold;
}

.contact-details p {
    margin-bottom: 10px;
}
