:root {
    --background-color: black;
    --text-color: whitesmoke;
    --gold-color: #FFD700;
    --green-royal: #007A33;
  }

/* About Us Styles for RTL */
.about-us-container {
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #333;
    text-align: right; /* Align text to the right for RTL */
    direction: rtl; /* Apply RTL layout */
}

.about-us-header {
    text-align: right; /* Align header text to the right */
    margin-bottom: 30px;
}
h1, h2, h3 {
    color: var(--gold-color);
}


.about-us-header h1 {
    text-align: center;
    font-size: 36px;
    font-weight: bold;
    
}

.about-us-header p {
    font-size: 18px;
    color: var(--text-color);
}

.about-us-content {
    margin-top: 50px;
}

.about-section {
    margin-bottom: 40px;
}

.about-section h2 {
    font-size: 28px;
    text-align: center;
    margin-bottom: 15px;
}

.about-section p {
    font-size: 18px;
    line-height: 1.6;
    color: var(--text-color);
}

.team-members {
    display: flex;
    justify-content: flex-start; /* Align team members starting from the right side */
    gap: 20px;
}

.team-member {
    text-align: center;
    width: 200px;
}

.team-member img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
}

.team-member h3 {
    font-size: 20px;
    
    margin-top: 10px;
}

.team-member p {
    font-size: 16px;
    color: var(--text-color);
}

.about-us-footer {
    text-align: right; /* Align footer content to the right */
    margin-top: 50px;
}

.about-us-footer h2 {
    font-size: 24px;
    font-weight: bold;
    
}

.about-us-footer p {
    font-size: 18px;
    color: var(--text-color);
    margin-bottom: 20px;
}

.about-us-footer button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: transparent;
    color: white;
    border: 1px solid var(--gold-color);;
    cursor: pointer;
    border-radius: 5px;
}

.about-us-footer button:hover {
    background-color: var(--gold-color);
    border: none;
    color: var(--background-color);
}
