/* Variables */
:root {
  --background-color: black;
  --text-color: whitesmoke;
  --gold-color: #FFD700;
  --green-royal: #007A33;
}

/* Resetting default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--background-color);
  font-family: Arial, Helvetica, sans-serif;
}

p, h1, h2, h3, h4, h5 {
  color: var(--text-color);
}

/* Responsive Styling */
@media (max-width: 768px) {
  .hero-content h1 {
      font-size: 2.5rem;
  }

  .hero-content p {
      font-size: 1rem;
  }
}

/* Mirror effect on hover */
/* Style for the crescent group with rotation effect */
.crescent {
  transition: transform 0.4s; /* Smooth transition for transformation */
  transform-origin: center; /* Set the transform origin to the center */
}

/* Rotate the crescent when hovered */
.hovered {
  transform: rotateY(180deg); /* Rotate around the Y-axis */
}