/* About Section */
.about-instructors {
    padding: 60px 20px;
    background: var(--background-color);
    text-align: center;
}

.about-instructors h1 {
    font-size: 2.5rem;
    color: var(--gold-color);
    margin-bottom: 40px;
}

/* Instructor Cards */
.instructor-cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

/* Individual Instructor Card */
.instructor-card {
    position: relative;
    perspective: 1000px;
    width: 250px; /* Postavite širinu kartice */
    height: 350px; /* Postavite visinu kartice */
}

.card-content {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    border-radius: 15px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.instructor-card.active .card-content {
    transform: rotateY(180deg);
}

.card-front,
.card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 15px;
}

.card-front {
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.card-back {
    background: #007A33;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: rotateY(180deg);
}

.instructor-image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 15px;
}

.instructor-card h3 {
    font-size: 1.8rem;
    margin: 10px 0;
    color: var(--background-color);
}
.instructor-card .card-back h3 {
    font-size: 1.8rem;
    margin: 10px 0;
    color: var(--gold-color);
}
.card-back p {
    padding: 10px;
    text-align: center;
}

.card-back button {
    margin-top: 10px;
    padding: 10px 15px;
    background-color: transparent;
    color: var(--gold-color);
    border: 1px solid var(--gold-color) ;
    border-radius: 5px;
    cursor: pointer;
}

/* Responsive styles */
@media (max-width: 768px) {
    .about-instructors h1 {
        font-size: 2rem;
    }

    .instructor-card {
        max-width: 90%;
        margin: 10px 0;
    }

    .instructor-card img.instructor-image {
        width: 80px;
        height: 80px;
    }
}
