/* Hero Section */
.hero {
    background: url('../images/quran.jpg') no-repeat center center/cover;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
}

.hero::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
}

.hero-content {
    position: relative;
    z-index: 2;
    text-align: center;
    padding: 20px;
}

.hero-content h1 {
    font-size: 3rem;
    color: var(--gold-color);
    margin-bottom: 15px;
    letter-spacing: 1px;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.7);
    display: block;
}

.hero-content p {
    font-size: 1.2rem;
    margin-bottom: 25px;
}

.hero-content button {
    padding: 12px 24px;
    color: var(--gold-color);
    background: transparent;
    border: 2px solid var(--gold-color);
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background 0.3s, color 0.3s;
}

.hero-content button:hover {
    background: var(--gold-color);
    color: white;
}

.hero-image {
    max-width: 100%;
    height: auto;
    margin: 0 auto;
    display: block;
}

/* Hero Content Animations */
.animated-text {
    font-size: 3rem;
    font-weight: bold;
    color: #FFD700;
    display: inline-block;
    animation: assembleText 3s ease forwards;
    letter-spacing: 2px;
}

@keyframes assembleText {
    0% {
        opacity: 0;
        transform: scale(0.2);
        filter: blur(10px);
    }
    50% {
        opacity: 0.7;
        transform: scale(1.2);
        filter: blur(5px);
    }
    100% {
        opacity: 1;
        transform: scale(1);
        filter: blur(0);
    }
}
